.form {
    width: 550px;
    margin: 5px auto;
    display: flex;
    flex-direction: column;
}

.inputTitle {
    font-size: 22px;
    margin-bottom: 3px;
}

.button {
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
    padding: 5px 10px;
    font-size: 20px;

    border-radius: 2px;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: flex;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-style: normal;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.button:hover,
.button:focus {
    background-color: rgb(25, 255, 132);
    scale: 1.03;
}

.title {
    margin: 5px;
}

.input {
    height: 40px;
    font-size: 25px;
    border: none;
    border-radius: 3px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}